export const mapBuffer = (ctor, buf, num, start, cstride, estride) => {
    const res = [];
    while (--num >= 0) {
        res.push(new ctor(buf, start, cstride));
        start += estride;
    }
    return res;
};
export const intoBuffer = (set, buf, src, start, cstride, estride) => {
    for (let v of src) {
        set(buf, v, start, 0, cstride, 1);
        start += estride;
    }
    return buf;
};
export function* vecIterator(ctor, buf, num, start, cstride, estride) {
    while (num-- > 0) {
        yield new ctor(buf, start, cstride);
        start += estride;
    }
}
export function* values(buf, num, start, stride) {
    while (num-- > 0) {
        yield buf[start];
        start += stride;
    }
}
