import { isArrayLike, isNumber } from "@thi.ng/checks";
import { asCSS } from "./convert";
/**
 * Takes a color in one of the following formats and tries to convert it
 * to a CSS string:
 *
 * - any IColor instance
 * - raw RGBA vector
 * - number ((A)RGB int)
 * - string (unchanged)
 *
 * @param col
 */
export const resolveAsCSS = (col) => isArrayLike(col)
    ? isNumber(col.mode)
        ? asCSS(col)
        : asCSS(col, 0 /* RGBA */)
    : isNumber(col)
        ? asCSS(col, 5 /* INT32 */)
        : col;
