import { ff, fattribs } from "./format";
export const rect = (p, width, height, attribs) => roundedRect(p, width, height, 0, 0, attribs);
export const roundedRect = (p, width, height, rx, ry, attribs) => {
    attribs = fattribs(Object.assign({}, attribs, { x: ff(p[0]), y: ff(p[1]), width: ff(width), height: ff(height) }));
    if (rx > 0 || ry > 0) {
        attribs.rx = ff(rx);
        attribs.ry = ff(ry);
    }
    return ["rect", attribs];
};
