import { fattribs, fcolor, ff } from "./format";
const RE_ALPHA_COLOR = /(rgb|hsl)a\(([a-z0-9.-]+),([0-9.%]+),([0-9.%]+),([0-9.]+)\)/;
const gradient = (type, attribs, stops) => [
    type,
    fattribs(attribs),
    ...stops.map(gradientStop)
];
const gradientStop = ([offset, col]) => {
    col = fcolor(col);
    // use stop-opacity attrib for safari compatibility
    // https://stackoverflow.com/a/26220870/294515
    let opacity;
    const parts = RE_ALPHA_COLOR.exec(col);
    if (parts) {
        col = `${parts[1]}(${parts[2]},${parts[3]},${parts[4]})`;
        opacity = parts[5];
    }
    return ["stop", { offset, "stop-color": col, "stop-opacity": opacity }];
};
export const linearGradient = (id, from, to, stops, attribs) => gradient("linearGradient", Object.assign({}, attribs, { id, x1: ff(from[0]), y1: ff(from[1]), x2: ff(to[0]), y2: ff(to[1]) }), stops);
export const radialGradient = (id, from, to, fr, r, stops, attribs) => gradient("radialGradient", Object.assign({}, attribs, { id, fx: ff(from[0]), fy: ff(from[1]), cx: ff(to[0]), cy: ff(to[1]), fr: ff(fr), r: ff(r) }), stops);
