import { eqDelta, eqDeltaFixed } from "@thi.ng/math";
import { add3, cross3, dot3, mulN3 } from "@thi.ng/vectors";
import { NONE } from "./api";
export const intersectPlanePlane = (na, wa, nb, wb) => {
    const dn = dot3(na, nb);
    if (eqDeltaFixed(dn, 1)) {
        return eqDelta(wa, wb) ? { type: 2 /* COINCIDENT */ } : NONE;
    }
    const det = 1 / (1 - dn * dn);
    const da = (wa - wb * dn) * det;
    const db = (wb - wa * dn) * det;
    return {
        type: 4 /* INTERSECT */,
        isec: [
            add3(null, mulN3([], na, da), mulN3([], nb, db)),
            cross3([], na, nb)
        ]
    };
};
