import { clamp01 } from "@thi.ng/math";
import { setC4 } from "@thi.ng/vectors";
import { ensureHue } from "./internal/ensure-hue";
/**
 * Converts a normalized hue to RGBA with given optional `alpha`
 * value (default: 1).
 *
 * @param out
 * @param hue
 */
export const hueRgba = (out, hue, alpha = 1) => {
    hue = ensureHue(hue) * 6;
    return setC4(out || [], clamp01(Math.abs(hue - 3) - 1), clamp01(2 - Math.abs(hue - 2)), clamp01(2 - Math.abs(hue - 4)), alpha);
};
export const namedHueRgba = (out, hue, alpha = 1) => hueRgba(out, hue / 12, alpha);
