/**
 * Returns new `Int32` wrapping given ARGB int.
 *
 * @param rgba
 */
export const int32 = (rgba) => new Int32(rgba);
/**
 * Returns new `Int32` wrapping given 24bit RGB color and setting alpha
 * channel set to 100% opaque.
 *
 * @param rgb
 */
export const int24 = (rgb) => new Int32((rgb & 0xffffff) | 0xff000000);
export class Int32 {
    constructor(col) {
        this.value = col >>> 0;
    }
    get mode() {
        return 5 /* INT32 */;
    }
    copy() {
        return new Int32(this.value);
    }
    deref() {
        return this.value;
    }
}
