// prettier-ignore
export const MATH = (op) => ([o, a, b]) => `${o}=${a}${op}${b};`;
// prettier-ignore
export const MATH_N = (op) => ([o, a]) => `${o}=${a}${op}n;`;
// prettier-ignore
export const SIGNED = (op) => ([o, a, b]) => `${o}=(${a}${op}${b})|0;`;
// prettier-ignore
export const UNSIGNED = (op) => ([o, a, b]) => `${o}=(${a}${op}${b})>>>0;`;
// prettier-ignore
export const SIGNED_N = (op) => ([o, a]) => `${o}=(${a}${op}n)|0;`;
// prettier-ignore
export const UNSIGNED_N = (op) => ([o, a]) => `${o}=(${a}${op}n)>>>0;`;
// prettier-ignore
export const FN = (op = "op") => ([o, a]) => `${o}=${op}(${a});`;
// prettier-ignore
export const FN2 = (op = "op") => ([o, a, b]) => `${o}=${op}(${a},${b});`;
// prettier-ignore
export const FN3 = (op = "op") => ([o, a, b, c]) => `${o}=${op}(${a},${b},${c});`;
// prettier-ignore
export const FN5 = (op = "op") => ([o, a, b, c, d, e]) => `${o}=${op}(${a},${b},${c},${d},${e});`;
// prettier-ignore
export const FN_N = (op = "op") => ([o, a]) => `${o}=${op}(${a},n);`;
export const DOT = ([a, b]) => `${a}*${b}`;
export const DOT_G = ([a, b]) => `s+=${a}*${b};`;
export const SET = ([o, a]) => `${o}=${a};`;
export const SET_N = ([a]) => `${a}=n;`;
export const ADDM = ([o, a, b, c]) => `${o}=(${a}+${b})*${c};`;
export const ADDM_N = ([o, a, b]) => `${o}=(${a}+${b})*n;`;
export const MADD = ([o, a, b, c]) => `${o}=${a}*${b}+${c};`;
export const MADD_N = ([o, a, b]) => `${o}=${a}*n+${b};`;
export const MIX = ([o, a, b, c]) => `${o}=${a}+(${b}-${a})*${c};`;
export const MIX_N = ([o, a, b]) => `${o}=${a}+(${b}-${a})*n;`;
export const SUBM = ([o, a, b, c]) => `${o}=(${a}-${b})*${c};`;
export const SUBM_N = ([o, a, b]) => `${o}=(${a}-${b})*n;`;
