import { setC3, setC4 } from "./setc";
/**
 * Sets `out` to `[a.x, a.y, n]`
 *
 * @param out
 * @param a
 * @param n
 */
export const setVN3 = (out, a, n) => setC3(out, a[0], a[1], n);
/**
 * Sets `out` to `[a.x, a.y, a.z, n]`
 *
 * @param out
 * @param a
 * @param n
 */
export const setVN4 = (out, a, n) => setC4(out, a[0], a[1], a[2], n);
